export function timeout (ms) {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

const browserPrefixes = ['webkit', 'Moz', 'ms', 'O']
const style = document.createElement('div').style

function capitalizeFirstLetter (str) {
  return str.charAt(0).toUpperCase() + str.slice(1)
}

function getExistStyle (styleName) {
  let existStyleName = styleName
  if (existStyleName in style) {
    return existStyleName
  }
  for (let i = 0; i < browserPrefixes.length; i++) {
    let prefix = browserPrefixes[i]
    existStyleName = `${prefix}${capitalizeFirstLetter(styleName)}`
    if (existStyleName in style) {
      return existStyleName
    }
  }
}

export function css (el, styleName, value) {
  let existStyle = getExistStyle(styleName)
  if (value !== undefined) {
    el.style[existStyle] = value
  } else {
    return el.style[existStyle]
  }
}

export function throttle (fn, delay = 500) {
  let timer
  return function () {
    let _args = arguments
    let _me = this
    if (timer) {
      return false
    }
    fn.apply(_me, _args)
    timer = setTimeout(function () {
      clearTimeout(timer)
      timer = null
    }, delay)
  }
}

export function debounce (fn, delay = 500) {
  let timer
  return function () {
    let _args = arguments
    let _me = this
    if (timer) {
      clearTimeout(timer)
      timer = null
    }
    timer = setTimeout(function () {
      fn.apply(_me, _args)
    }, delay)
  }
}

export function formatLyric (lyric) {
  if (!lyric) return
  const metaReg = /^\[(\D\w*):(\S*)\]$/
  const lineReg = /^\[(\d+):(\d+)\.(\d+)\](\S+.+)/
  const emptyReg = /^\[(\d+):(\d+):(\d+)\](\S+.+)/
  const meta = {}
  const lines = []
  lyric.split('\n').forEach(line => {
    let result = lineReg.exec(line)
    if (result) {
      const time = parseInt(result[1]) * 60000 + parseInt(result[2]) * 1000 + parseInt(result[3]) * 10
      const value = result[4]
      lines.push({ time, value })
    } else {
      result = emptyReg.exec(line)
      if (result) {
        const time = parseInt(result[1]) * 60000 + parseInt(result[2]) * 1000 + parseInt(result[3]) * 10
        const value = result[4]
        lines.push({ time, value })
      } else {
        result = metaReg.exec(line)
        if (result) {
          meta[result[1]] = result[2]
        }
      }
    }
  })
  // 如果没歌词则显示提示
  if (lines.length === 0) {
    lines.push({
      time: 0, value: '无歌词'
    })
  }
  return { meta, lines }
}

export function createElement (tag = 'div') {
  const el = document.createElement(tag)
  document.body.appendChild(el)
  return el
}

export function extend (target, source) {
  for (let key in source) {
    target[key] = source[key]
  }
  return target
}

export const isIOS = /iphone|ipad/.test(navigator.userAgent.toLowerCase())



