<template lang="html">
  <icon name="spinner" :scale="scale" spin></icon>
</template>

<script>
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/spinner'

  export default {
    props: {
      scale: {
        type: Number,
        default: 1
      }
    },
    components: { Icon }
  }
</script>

<style scoped lang="less">
  svg {
    animation-timing-function: steps(8);
    animation-duration: .5s;
  }
</style>



