<template lang="html">
  <transition name="slideUp">
    <div class="add-song" ref="addSong" v-show="showFlag">
      <div class="header">
        <div class="icon-close" @click="hide">
          <icon name="close" scale="1.5"></icon>
        </div>
        <div class="title">
          添加歌曲
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="searchBox" @change="queryChange"></search-box>
      </div>
      <div class="tab-wrapper" v-show="!query">
        <tab-btn :list="tabList" :index="tabIndex" @change="tabChange"></tab-btn>
      </div>
      <div class="shortcut-wrapper" v-show="!query">
        <scroller :refreshDelay="refreshDelay" class="shortcut" ref="scroller" :data="searchHistory">
          <song-list :songs="playHistory" v-show="tabIndex == 0" class="song-list" @selectItem="selectSong" :editable="false"></song-list>
          <search-list
          :search="searchHistory"
          :showTitle="false"
          @select="setQuery"
          @del="decacheSearch"
          @delAll="clearSearchHistory"
          v-show="tabIndex == 1"
          ></search-list>
        </scroller>
      </div>
      <div class="search-result" v-show="query" ref="searchResult" @touchstart="blurInput">
        <suggest ref="suggest" :showSinger="false" :query="query" @select="saveSearch"></suggest>
      </div>
    </div>
  </transition>
</template>

<script>
  import * as atypes from '@/store/action-types'
  import { mapGetters, mapActions } from 'vuex'
  import { timeout } from 'assets/js/util'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/close'
  import Scroller from 'components/base/scroller/scroller'
  import SearchBox from 'components/base/search-box/search-box'
  import SearchList from 'components/base/search-list/search-list'
  import SongList from 'components/base/song-list/song-list'
  import TabBtn from 'components/base/tab-btn/tab-btn'
  import Suggest from 'components/suggest/suggest'
  import SearchMixin from '@/mixins/search-mixin'

  export default {
    name: 'add-song',
    mixins: [SearchMixin],
    components: { Icon, Scroller, SearchBox, SearchList, Suggest, SongList, TabBtn },
    data () {
      return {
        showFlag: false,
        tabList: ['最近播放', '搜索历史'],
        tabIndex: 0
      }
    },
    computed: {
      ...mapGetters([
        'playHistory'
      ])
    },
    watch: {
      async tabIndex () {
        this.$refs.scroller.scrollTo(0, 0)
        await timeout(20)
        this.$refs.scroller.refresh()
      }
    },
    methods: {
      ...mapActions({
        addSong: atypes.ADD_SONG_AND_PLAY
      }),
      async show () {
        this.showFlag = true
        await timeout(this.refreshDelay)
        if (this.query) {
          this.$refs.suggest.refresh()
        } else {
          this.$refs.scroller.refresh()
        }
      },
      hide () {
        this.showFlag = false
      },
      tabChange (index) {
        this.tabIndex = index
      },
      selectSong (song, index) {
        if (index === 0) return
        this.addSong(song)
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .add-song {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: @color-background;
    color: @color-text;
    display: flex;
    flex-direction: column;
  }

  .header {
    width: 70%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: @font-size-large;
    margin: 5px auto;
    .icon-close {
      .extend-click();
      color: @color-theme;
      position: absolute;
      top: 10px;
      right: 10px;
    }
  }

  .search-box-wrapper {
    padding: 0 20px;
  }

  .shortcut-wrapper {
    padding: 0 10px 10px;
    box-sizing: border-box;
    flex: 1;
    overflow: hidden;
  }

  .shortcut {
    height: 100%;
    overflow: hidden;
  }

  .tab-wrapper {
    padding: 20px 20px 10px;
  }

  .song-list {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .search-result {
    position: absolute;
    left: 0;
    width: 100%;
    top: 110px;
    bottom: 0;
  }

  .slideUp-enter-active, .slideUp-leave-active {
    transition: all .25s;
  }
  .slideUp-enter, .slideUp-leave-to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
</style>



