export default {

  data () {
    return {
      toasts: [],
      delay: 3000
    }
  },

  destroyed () {
    const parent = this.$el.parentNode
    parent.removeChild(this.$el)
  },

  methods: {
    show (toast) {
      const toastId = Date.now()
      toast.id = toastId
      this.toasts.unshift(toast)
      setTimeout(() => {
        this.hide(toastId)
      }, this.delay)
    },
    hide (toastId) {
      const index = this.toasts.findIndex(toast => toast.id === toastId)
      if (index >= 0) this.toasts.splice(index, 1)
    },
    clear () {
      this.toasts = []
    }
  }
}



