<template lang="html">
  <div class="suggest">
    <scroller class="list-wrapper" ref="scroller" :data="result" :pullupLoad="loadMore" :hasMore="hasMore">
      <ul class="list">
        <li @click="selectItem(item)" class="item" v-for="item in result">
          <div class="icon">
            <icon :name="getIcon(item.type)"></icon>
          </div>
          <div class="name">
            <p class="text" v-html="getDisplayName(item)"></p>
          </div>
        </li>
      </ul>
    </scroller>
    <div class="no-result-wrapper" v-show="!hasMore && !result.length">
      <no-result title="抱歉，暂无搜索结果"></no-result>
    </div>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'
  import * as atypes from '@/store/action-types'
  import Scroller from 'components/base/scroller/scroller'
  import Loading from 'components/base/loading/loading3'
  import NoResult from 'components/base/no-result/no-result'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/user'
  import 'vue-awesome/icons/music'
  import { search } from 'api/search'
  import { ERR_OK } from 'api/config'
  import { catchErrors } from 'api/handler'
  import { debounce } from 'assets/js/util'
  import Song from 'assets/js/song'

  const TYPE_SINGER = 'singer'
  const PAGE_SIZE = 20

  export default {
    name: 'suggest',
    props: {
      query: {
        type: String,
        default: ''
      },
      showSinger: {
        type: Boolean,
        default: true
      }
    },
    components: { Scroller, Icon, Loading, NoResult },
    data () {
      return {
        page: 1,
        result: [],
        loading: false,
        hasMore: true
      }
    },
    watch: {
      query () {
        this.reset()
        this.search()
      }
    },
    methods: {
      ...mapActions({
        addSong: atypes.ADD_SONG_AND_PLAY
      }),
      reset () {
        this.page = 1
        this.hasMore = true
        this.result = []
        this.$refs.scroller.scrollTo(0, 0)
      },
      _getSearchData: catchErrors(async function () {
        const searchRes = await search({
          query: this.query, page: this.page, pageSize: PAGE_SIZE, zhida: this.showSinger
        })
        if (searchRes && searchRes.code === ERR_OK) {
          return searchRes.data
        }
      }),
      search: debounce(catchErrors(async function () {
        const data = await this._getSearchData()
        this.result = this._formatResult(data)
        this._checkMore(data)
      })),
      loadMore: catchErrors(async function (done) {
        if (!this.hasMore) return
        this.page ++
        const data = await this._getSearchData()
        this.result = this.result.concat(this._formatResult(data))
        this._checkMore(data)
        done && done()
      }),
      _checkMore (data) {
        const { list = [], curpage = 1, totalnum = 0 } = data.song || {}
        if (list.length < 1 || curpage * PAGE_SIZE >= totalnum) {
          this.hasMore = false
        } else {
          this.hasMore = true
        }
      },
      _formatResult (data) {
        let result = []
        if (data.zhida && data.zhida.singerid) {
          result.push({
            ...data.zhida,
            type: TYPE_SINGER
          })
        }
        if (data.song) {
          result = [
            ...result,
            ...this._formatSongs(data.song.list)
          ]
        }
        return result
      },
      /**
       * [格式化歌曲数据]
       * @param {Array} songs 接口获取的歌曲数据
       */
      _formatSongs (songs) {
        const formattedSongs = []
        songs.forEach(song => {
          if (song.songid && song.albummid) {
            formattedSongs.push(Song.create(song))
          }
        })
        return formattedSongs
      },
      getIcon (type) {
        if (type === TYPE_SINGER) return 'user'
        return 'music'
      },
      getDisplayName (item) {
        if (item.type === TYPE_SINGER) return item.singername
        return `${item.name} - ${item.singer}`
      },
      selectItem (item) {
        if (item.type === TYPE_SINGER) {
          this.$router.push(`/search/singer/${item.singermid}`)
        } else {
          this.addSong(item)
        }
        this.$emit('select', item)
      },
      refresh () {
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .suggest {
    position: relative;
    height: 100%;
    overflow: hidden;
    .list-wrapper {
      height: 100%;
    }
    .list {
      padding: 0 30px;
      .item {
        display: flex;
        align-items: center;
        padding-bottom: 16px;
      }
      .icon {
        flex: 0 0 30px;
        width: 30px;
        font-size: 14px;
        color: @color-text-d;
      }
      .name {
        flex: 1;
        font-size: @font-size-medium;
        color: @color-text-d;
        overflow: hidden;
        .text {
          .no-wrap()
        }
      }
    }
    .no-result-wrapper {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-75%);
      text-align: center;
    }
  }
</style>



