import * as types from './mutation-types'

const mutations = {
  [types.SET_RECOMMEND_DISC_LIST] (state, recommendDiscList) {
    state.recommendDiscList = recommendDiscList
  },
  [types.SET_SINGER_LIST] (state, singerGroupList) {
    state.singerGroupList = singerGroupList
  },
  [types.SET_TOP_LIST] (state, topList) {
    state.topList = topList
  },
  [types.SET_SEARCH_HISTORY] (state, search) {
    state.searchHistory = search
  },
  [types.SET_FAVORITE] (state, favorite) {
    state.favorite = favorite
  },
  [types.SET_COLLECT_DISC] (state, discList) {
    state.collectDiscList = discList
  },
  [types.CREATE_MY_DISC] (state, newDisc) {
    state.myDiscList.unshift(newDisc)
  },
  [types.DELETE_MY_DISC] (state, disc) {
    const index = state.myDiscList.findIndex(v => v.id === disc.id)
    if (index >= 0) {
      state.myDiscList.splice(index, 1)
    }
  },
  [types.DELETE_MY_DISC_ALL] (state) {
    state.myDiscList = []
  },
  [types.ADD_SONG_TO_MY_DISC] (state, { dissid, song }) {
    const myDiscList = state.myDiscList.slice()
    const disc = myDiscList.find(v => v.id === dissid)
    if (disc && disc.songs) {
      const index = disc.songs.findIndex(v => v.id === song.id)
      if (index >= 0) {
        window.$toast.error('歌曲已存在')
      } else {
        disc.songs.unshift(song)
        disc.imgurl = disc.songs[0].image
        window.$toast.success('加入歌单')
      }
    }
    state.myDiscList = myDiscList
  },
  [types.REMOVE_SONG_FROM_MY_DISC] (state, { dissid, songid }) {
    const myDiscList = state.myDiscList.slice()
    const disc = myDiscList.find(v => v.id === dissid)
    if (disc && disc.songs) {
      const index = disc.songs.findIndex(v => v.id === songid)
      if (index >= 0) {
        disc.songs.splice(index, 1)
        disc.imgurl = disc.songs[0] ? disc.songs[0].image : ''
      }
    }
    state.myDiscList = myDiscList
  },
  [types.CLEAR_MY_DISC] (state, dissid) {
    const myDiscList = state.myDiscList.slice()
    const disc = myDiscList.find(v => v.id === dissid)
    if (disc) {
      disc.songs = []
      disc.imgurl = ''
    }
    state.myDiscList = myDiscList
  },
  [types.CHANGE_MY_DISC_NAME] (state, { dissid, name }) {
    const myDiscList = state.myDiscList.slice()
    const disc = myDiscList.find(v => v.id === dissid)
    if (disc) {
      disc.name = disc.creator = name
    }
    state.myDiscList = myDiscList
  }
}

export default mutations



