<template lang="html">
  <div class="search-history" v-show="search.length">
    <h1 class="title" v-show="showTitle">
      <span class="text">搜索历史</span>
      <span class="clear" @click="delAll">
        <icon class="icon-clear" name="trash-o" scale="1.2"></icon>
      </span>
    </h1>
    <div class="search-list">
      <transition-group name="list" tag="ul">
        <li class="item" :key="item" v-for="item in search" @click="select(item)">
          <span class="text">{{ item }}</span>
          <span class="icon" @click.stop="del(item)">
            <icon class="icon-delete" name="remove"></icon>
          </span>
        </li>
      </transition-group>
    </div>
  </div>
</template>

<script>
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/remove'

  export default {
    name: 'search-list',
    props: {
      search: {
        type: Array,
        default: []
      },
      showTitle: {
        type: Boolean,
        default: true
      }
    },
    components: { Icon },
    methods: {
      select (item) {
        this.$emit('select', item)
      },
      del (item) {
        this.$emit('del', item)
      },
      delAll () {
        this.$emit('delAll')
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .search-history {
    position: relative;
    margin: 0 20px;
    .title {
      display: flex;
      align-items: center;
      height: 40px;
      font-size: @font-size-medium;
      color: @color-text-l;
      .text {
        flex: 1;
      }
      .clear {
        .extend-click();
        .icon-clear {
          color: @color-text-d;
        }
      }
    }
  }

  .search-list {
    .item {
      display: flex;
      align-items: center;
      height: 36px;
      overflow: hidden;
      font-size: @font-size-medium;
      &.list-enter-active, &.list-leave-active {
        transition: all .25s;
      }
      &.list-enter, &.list-leave-to {
        height: 0;
        opacity: 0;
      }
      .text {
        flex: 1;
        color: @color-text-l;
      }
      .icon {
        .extend-click();
        .icon-delete {
          color: @color-text-d;
        }
      }
    }
  }

</style>



