<template>
  <div class="m-header">
    <div class="icon"></div>
    <h1 class="text">Boxfish Music</h1>
    <router-link tag="div" class="user" to="/user">
      <icon class="icon-user" name="user-o" scale="1.25"></icon>
    </router-link>
  </div>
</template>

<script>
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/user-o'

  export default {
    name: 'm-header',
    components: { Icon }
  }
</script>

<style scoped lang="less" rel="stylesheet/less">
  @import '~assets/less/variable';
  @import '~assets/less/mixin';

  .m-header {
    position: relative;
    width: 100%;
    height: 44px;
    text-align: center;
    color: @color-theme;
    background: @color-background;
    z-index: 10;
    font-size: 0;
    .icon {
      display: inline-block;
      vertical-align: top;
      margin-top: 6px;
      width: 30px;
      height: 32px;
      margin-right: 9px;
      background-image: url(~assets/images/default.png);
      background-size: 30px 32px;
    }
    .text {
      display: inline-block;
      vertical-align: top;
      line-height: 44px;
      font-size: @font-size-large;
    }
    .user {
      .extend-click();
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
</style>



