<template lang="html">
  <transition name="fadeIn">
    <div class="toast-wrapper" v-show="toasts.length">
      <transition-group tag="div" class="toast-list" name="list">
        <div :key="toast.id" v-for="toast in toasts" class="toast" :class="toast.type" @click="hide(toast.id)">
          <div class="message">{{ toast.message }}</div>
        </div>
      </transition-group>
    </div>
  </transition>
</template>

<script>
  import mixin from './mixin'

  export default {
    name: 'toast',
    mixins: [mixin]
  }
</script>

<style scoped lang="less">
  @transitionDuration: .25s;
  @translate: translateX(-50%);
  @toastHeight: 36px;

  .fadeIn-enter-active, .fadeIn-leave-active {
    transition: opacity @transitionDuration;
  }
  .fadeIn-enter, .fadeIn-leave-to {
    opacity: 0;
  }

  .list-enter-active {
    transition: all @transitionDuration;
  }
  .list-leave-active {
    transition: all @transitionDuration*2;
  }
  .list-enter {
    margin-top: -@toastHeight !important;
    opacity: 0;
  }
  .list-leave-to {
    opacity: 0;
  }

  .toast-wrapper {
    position: fixed;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 20000;
  }

  .toast {
    color: #fff;
    font-size: 14px;
    border-radius: 4px;
    height: @toastHeight;
    line-height: @toastHeight;
    padding: 0 20px;
    margin-top: 10px;
    &.success {
      background-color: rgba(33, 200, 69, .7);
    }
    &.info {
      background-color: rgba(33, 133, 255, .7);
    }
    &.error {
      background-color: rgba(220, 20, 20, .7);
    }
  }
</style>



