import Store from 'store/dist/store.modern'
import Song from 'assets/js/song'

const SEARCH_KEY = '__search__'
const SEARCH_MAX_LEN = 15

const PLAY_KEY = '__play__'
const PLAY_MAX_LEN = 100

const FAVORITE_KEY = '__favorite__'
const FAVORITE_MAX_LEN = 100

const COLLECT_KEY = '__collect__'
const COLLECT_MAX_LEN = 100

const MY_KEY = '__my__'

function deleteArray (arr = [], val = '', compare = v => v === val) {
  const index = arr.findIndex(compare)
  if (index >= 0) arr.splice(index, 1)
}
function insertArray (arr = [], val = '', maxlen = 10, compare = v => v === val) {
  const index = arr.findIndex(compare)
  if (index === 0) return
  if (index > 0) arr.splice(index, 1)
  arr.unshift(val)
  if (maxlen && arr.length > maxlen) arr.pop()
}

// 最近的搜索
export function cacheSearch (query) {
  const search = Store.get(SEARCH_KEY) || []
  if (query !== undefined) {
    insertArray(search, query, SEARCH_MAX_LEN)
    Store.set(SEARCH_KEY, search)
  }
  return search
}
export function decacheSearch (query) {
  if (query === undefined) {
    Store.remove(SEARCH_KEY)
    return []
  }
  const search = Store.get(SEARCH_KEY)
  deleteArray(search, query)
  Store.set(SEARCH_KEY, search)
  return search
}

// 最近播放
export function cachePlay (song) {
  const play = Store.get(PLAY_KEY) || []
  if (song !== undefined) {
    insertArray(play, song, PLAY_MAX_LEN, v => v.id === song.id)
    Store.set(PLAY_KEY, play)
  }
  return play.map(song => new Song(song))
}
export function decachePlay (song) {
  if (song === undefined) {
    Store.remove(PLAY_KEY)
    return []
  }
  const play = Store.get(PLAY_KEY)
  deleteArray(play, song, v => v.id === song.id)
  Store.set(PLAY_KEY, play)
  return play.map(song => new Song(song))
}

// 喜欢的歌
export function cacheFavorite (song) {
  const favorite = Store.get(FAVORITE_KEY) || []
  if (song !== undefined) {
    insertArray(favorite, song, FAVORITE_MAX_LEN, v => v.id === song.id)
    Store.set(FAVORITE_KEY, favorite)
  }
  return favorite.map(song => new Song(song))
}
export function decacheFavorite (song) {
  if (song === undefined) {
    Store.remove(FAVORITE_KEY)
    return []
  }
  const favorite = Store.get(FAVORITE_KEY)
  deleteArray(favorite, song, v => v.id === song.id)
  Store.set(FAVORITE_KEY, favorite)
  return favorite.map(song => new Song(song))
}

// 收藏的歌单
export function cacheCollect (disc) {
  const collect = Store.get(COLLECT_KEY) || []
  if (disc !== undefined) {
    insertArray(collect, disc, COLLECT_MAX_LEN, v => v.id === disc.id)
    Store.set(COLLECT_KEY, collect)
  }
  return collect
}
export function decacheCollect (disc) {
  if (disc === undefined) {
    Store.remove(COLLECT_KEY)
    return []
  }
  const collect = Store.get(COLLECT_KEY)
  deleteArray(collect, disc, v => v.id === disc.id)
  Store.set(COLLECT_KEY, collect)
  return collect
}

// 我的歌单
export function cacheMyDiscList (myDiscList) {
  if (myDiscList) {
    Store.set(MY_KEY, myDiscList)
  } else {
    return Store.get(MY_KEY) || []
  }
}



