import jsonp from 'assets/js/jsonp'
import axios from 'axios'
import { commonParams, jsonpOptions } from './config'

export function getRecommend () {
  const url = `https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg`
  const postData = {
    ...commonParams,
    platform: 'h5',
    uin: 0,
    needNewCode: 1
  }
  return jsonp(url, postData, jsonpOptions)
}

export function getDiscList () {
  const url = `/api/getDiscList`
  const postData = {
    ...commonParams,
    platform: 'yqq',
    hostUin: 0,
    sin: 0,
    ein: 29,
    sortId: 5,
    categoryId: 10000000,
    needNewCode: 0,
    rnd: Math.random(),
    format: 'json'
  }
  return axios.get(url, { params: postData }).then(res => res.data)
}

