export function getDiscSongList (disstid) {
  const url = `/api/getDiscSongList`
  const postData = {
    ...commonParams,
    disstid,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    g_tk: 5381
  }
  return axios.get(url, { params: postData }).then(res => res.data)
}



