<template lang="html">
  <div class="song-list">
    <div class="loading-wrapper" v-show="songs.length == 0 && showLoading">
      <loading :scale="1.5"></loading>
    </div>
    <ul>
      <li @click="selectSong(song, index)" class="item" v-for="(song, index) in songs">
        <div class="rank" v-show="rank">
          <icon class="icon-trophy" name="trophy" scale="2" v-show="index < 3" :style="{ color: getTrophyColor(index) }"></icon>
          <span class="num" v-show="index > 2">{{ index + 1 }}</span>
        </div>
        <div class="content">
          <h2 class="name">{{ song.name }}</h2>
          <p class="desc">{{ song.singer }}·{{ song.album }}</p>
        </div>
        <div class="edit" v-show="editable" @click.stop="edit($event, song, index)">
          <icon name="ellipsis-v" scale="1.25"></icon>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Loading from 'components/base/loading/loading3'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/trophy'
import 'vue-awesome/icons/ellipsis-v'

export default {
  name: 'song-list',
  props: {
    songs: {
      type: Array,
      default: () => []
    },
    showLoading: {
      type: Boolean,
      default: true
    },
    rank: {
      type: Boolean,
      default: false
    },
    editable: {
      type: Boolean,
      default: true
    }
  },
  components: { Loading, Icon },
  methods: {
    selectSong (song, index) {
      this.$emit('selectItem', song, index)
    },
    getTrophyColor (index) {
      return ['#D9D919 ', '#eeeeee', '#B5A642 '][index]
    },
    edit (event, song, index) {
      this.$emit('edit', event, song, index)
    }
  }
}
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .song-list {
    .item {
      display: flex;
      align-items: center;
      box-sizing: border-box;
      height: 64px;
      font-size: @font-size-medium;
      .rank {
        width: 25px;
        margin-right: 25px;
        text-align: center;
        color: @color-theme;
      }
      .edit {
        .extend-click();
        margin-left: 20px;
        color: #fff;
      }
      .content {
        flex: 1;
        line-height: 20px;
        overflow: hidden;
        .name {
          .no-wrap();
          color: @color-text;
        }
        .desc {
          .no-wrap();
          margin-top: 4px;
          color: @color-text-d;
        }
      }
    }
  }

  .loading-wrapper {
    margin-top: 20px;
    text-align: center;
    color: @color-theme;
  }

</style>



