import * as mtypes from '@/store/mutation-types'
import * as atypes from '@/store/action-types'
import { LOOP, RANDOM } from 'assets/js/config'
import { cachePlay, decachePlay } from 'assets/js/cache'

const actions = {
  [atypes.SELECT_AND_PLAY] ({ commit, state }, { list, index }) {
    commit(mtypes.SET_PLAYLIST, list)
    commit(mtypes.SET_FULLSCREEN, true)
    commit(mtypes.SET_CURRENT_INDEX, index)
    commit(mtypes.SET_PLAYING, true)
  },
  [atypes.CHANGE_SONG] ({ commit, state }) {
    const { mode } = state
    if (mode === LOOP) {
      commit(mtypes.NEXT)
    } else if (mode === RANDOM) {
      commit(mtypes.RANDOM)
    }
  },
  [atypes.RANDOM_PLAY] ({ dispatch, commit, state }, list) {
    commit(mtypes.SET_PLAYLIST, list)
    commit(mtypes.SET_MODE, RANDOM)
    commit(mtypes.RANDOM)
    commit(mtypes.SET_FULLSCREEN, true)
  },
  /**
   * 添加歌曲到播放列表
   */
  [atypes.ADD_SONG_AND_PLAY] ({ commit, state }, song) {
    const index = state.playlist.findIndex(s => s.id === song.id)
    if (index >= 0) {
      commit(mtypes.SET_CURRENT_INDEX, index)
    } else {
      commit(mtypes.ADD_SONG, song)
      if (state.playlist.length === 1) {
        commit(mtypes.SET_FULLSCREEN, true)
      }
      commit(mtypes.SET_CURRENT_INDEX, state.playlist.length - 1)
    }
    window.$toast.success('添加歌曲至列表')
  },
  /**
   * 从歌曲列表中删除歌曲
   */
  [atypes.REMOVE_SONG] ({ commit, state }, index) {
    if (state.index > index || index === state.playlist.length - 1) {
      commit(mtypes.SET_CURRENT_INDEX, state.index - 1)
    }
    const playlist = state.playlist.slice()
    playlist.splice(index, 1)
    commit(mtypes.SET_PLAYLIST, playlist)
    if (playlist.length < 1) {
      commit(mtypes.SET_PLAYING, false)
      commit(mtypes.SET_FULLSCREEN, false)
      commit(mtypes.SET_CURRENT_INDEX, -1)
    }
  },
  /**
   * 清除歌曲列表
   */
  [atypes.CLEAR_PLAYLIST] ({ commit, state }) {
    commit(mtypes.SET_CURRENT_INDEX, -1)
    commit(mtypes.SET_FULLSCREEN, false)
    commit(mtypes.SET_PLAYING, false)
    commit(mtypes.SET_PLAYLIST, [])
  },
  /**
   * 缓存播放历史
   */
  [atypes.CACHE_PLAY] ({ commit }, song) {
    const play = cachePlay(song)
    commit(mtypes.SET_PLAY_HISTORY, play)
  },
  /**
   * 删除缓存播放历史
   */
  [atypes.DECACHE_PLAY] ({ commit }, song) {
    const play = decachePlay(song)
    commit(mtypes.SET_PLAY_HISTORY, play)
  },
  /**
   * 删除所有缓存播放历史
   */
  [atypes.DECACHE_PLAY_ALL] ({ commit }) {
    const play = decachePlay()
    commit(mtypes.SET_PLAY_HISTORY, play)
  }
}

export default actions



