<template lang="html">
  <div class="no-result">
    <img :src="icon" :alt="code">
    <h2 class="title">{{ title }}</h2>
  </div>
</template>

<script>
  export default {
    name: 'no-result',
    props: {
      title: {
        type: String,
        default: ''
      },
      code: {
        type: String,
        default: '404'
      }
    },
    computed: {
      icon () {
        return require(`assets/images/${this.code}.png`)
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";

  .no-result {
    font-size: @font-size-medium;
    img {
      display: block;
      width: 30%;
      margin: 10px auto;
    }
  }
</style>



