<template lang="html">
  <div class="container" @click="hide">
    <backdrop ref="backdrop"></backdrop>
    <transition name="popup">
      <div class="popup" v-show="state == 1" :style="popupStyle">
        <div class="action" v-for="action in actions" @click="action.operation">
          {{ action.desc }}
        </div>
        <div class="arrow" :style="arrowStyle" :class="arrowClass"></div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Backdrop from './backdrop'
  import mixin from './mixin'

  export default {
    name: 'popup',
    mixins: [mixin],
    components: { Backdrop },
    data () {
      return {
        x: 0,
        y: 0,
        state: 0,
        actions: []
      }
    },
    computed: {
      left () {
        return this.x / window.innerWidth
      },
      top () {
        return this.y / window.innerHeight
      },
      popupStyle () {
        if (this.top < 0.5) {
          return {
            top: `${this.top * window.innerHeight + 20}px`,
            transformOrigin: `${this.left * 100}% 0%`
          }
        }
        return {
          bottom: `${(1 - this.top) * window.innerHeight + 20}px`,
          transformOrigin: `${this.left * 100}% 100%`
        }
      },
      arrowClass () {
        if (this.top < 0.5) {
          return 'top'
        }
        return 'bottom'
      },
      arrowStyle () {
        return {
          left: `${this.left * 100}%`
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @import "./popup.less";

  .popup {
    position: fixed;
    left: 3%;
    width: 94%;
    text-align: left;
    z-index: 10001;
    background-color: rgba(255, 255, 255, 1);
    color: #000;
    font-size: 14px;
    box-sizing: border-box;
    border-radius: 4px;
    transform: scale(1);
    .arrow {
      display: block;
      content: "";
      width: @arrowWidth;
      height: @arrowWidth;
      background-color: inherit;
      position: absolute;
      transform: rotate(45deg);
      border-radius: 2px;
      &.top {
        top: -@arrowWidth/2 + 1px;
      }
      &.bottom {
        bottom: -@arrowWidth/2 + 1px;
      }
    }
  }

  .action {
    margin: 0 10px;
    padding: 14px 10px;
    border-bottom: 1px solid #eee;
    &:last-child {
      border-bottom: none;
    }
  }

</style>



