<template lang="html">
  <div class="search-box">
    <icon class="icon-search" name="search"></icon>
    <input type="text" name="query" v-model="query" class="box" :placeholder="placeholder" ref="query">
    <div class="icon-clear" @click="clear">
      <icon name="close" v-show="query"></icon>
    </div>
  </div>
</template>

<script>
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/search'
  import 'vue-awesome/icons/close'

  export default {
    name: 'search-box',
    props: {
      placeholder: {
        type: String,
        default: '搜索歌曲、歌手'
      }
    },
    components: { Icon },
    data () {
      return {
        query: ''
      }
    },
    watch: {
      query () {
        this.$emit('change', this.query)
      }
    },
    methods: {
      clear () {
        this.query = ''
      },
      setQuery (query) {
        this.query = query
      },
      blur () {
        this.$refs.query.blur()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";

  .search-box {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100;
    padding: 0 6px;
    height: 40px;
    background: @color-highlight-background;
    border-radius: 6px;
    .icon-search, .icon-clear {
      color: @color-background;
      margin: 5px;
      line-height: 0;
    }
    .box {
      flex: 1;
      margin: 0 5px;
      background: @color-highlight-background;
      color: @color-text;
      font-size: font-size-medium;
      &::placeholder {
        color: @color-text-d;
      }
    }
  }
</style>



