<template lang="html">
  <div class="container">
    <backdrop ref="backdrop"></backdrop>
    <transition name="popup">
      <div class="prompt" v-show="state == 1">
        <div class="content">
          <div class="title" v-show="title">{{ title }}</div>
          <div class="input">
            <input type="text" v-model="text" :placeholder="placeholder">
          </div>
        </div>
        <div class="control">
          <div class="btn btn-cancel" @click="onCancel">{{ cancelText }}</div>
          <div class="btn btn-confirm" @click="onConfirm">{{ confirmText }}</div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Backdrop from './backdrop'
  import mixin from './mixin'

  export default {
    name: 'confirm',
    mixins: [mixin],
    components: { Backdrop },
    data () {
      return {
        state: 0,
        title: '',
        text: '',
        placeholder: '',
        cancelText: '取消',
        confirmText: '确定'
      }
    },
    methods: {
      reset () {
        this.text = ''
        this.placeholder = ''
      },
      onConfirm () {
        this.hide()
        this.confirm && this.confirm(this.text)
        this.reset()
      },
      onCancel () {
        this.hide()
        this.cancel && this.cancel()
        this.reset()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "./dialog.less";

  .prompt {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: @translate;
    width: @width;
    text-align: center;
    z-index: 10001;
    background-color: rgba(255, 255, 255, 1);
    color: @color-highlight-background;
    font-size: @font-size-medium;
  }

  .content {
    padding: 10px;
    border-bottom: 1px solid #eee;
  }
  .title {
    margin: 10px 0 15px;
    box-sizing: border-box;
    font-weight: bold;
  }
  .input {
    margin: 10px 0 0;
    font-size: .8em;
    box-sizing: border-box;
    padding: 10px;
    background-color: inherit;
    border: 1px solid #eee;
    text-align: left;
    input {
      background-color: inherit;
    }
  }
  .control {
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    .btn {
      padding: 10px 0;
      flex: 1;
    }
    .btn-confirm {
      background-color: #4a90e2;
      border: 1px solid #4a90e2;
      color: #fff;
    }
    .btn-cancel {
      border: 1px solid #e5e5e5;
      margin-right: 10px;
    }
  }
</style>



