<template lang="html">
  <div class="wrapper" @click="moveTo">
    <div class="progress-bar" ref="progressBar">
      <div class="progress" :style="{ width: percent }"></div>
      <div
      class="progress-btn"
      :style="{ left: percent }"
      @touchmove.stop.prevent="drag"
      @touchend.stop="dragend"
      @touchstart.stop="dragstart"
      ></div>
    </div>
  </div>
</template>

<script>
import { timeout } from 'assets/js/util'

export default {
  name: 'progress-bar',
  props: {
    progress: {
      type: Number,
      default: 0
    },
    jumpTo: Function,
    jump: Function
  },
  created () {
    this.startX = 0
    this.dragStartProgress = 0
  },
  data () {
    return {
      dragging: false,
      dragProgress: 0
    }
  },
  computed: {
    percent () {
      if (this.dragging) {
        return `${this.dragProgress * 100}%`
      }
      return `${this.progress * 100}%`
    }
  },
  methods: {
    moveTo (e) {
      if (e.target.className.includes('progress-btn')) return
      const progress = e.offsetX / this.$refs.progressBar.clientWidth
      if (this.jumpTo) {
        this.jumpTo(progress)
      }
    },
    dragstart (e) {
      this.dragging = true
      this.startX = e.touches[0].clientX
      this.dragStartProgress = this.dragProgress = this.progress
    },
    drag (e) {
      if (!this.dragging) return
      const deltaX = e.touches[0].clientX - this.startX
      const destProgress = this.dragStartProgress + deltaX / this.$refs.progressBar.clientWidth
      this.dragProgress = destProgress >= 1 ? 1 : (destProgress <= 0 ? 0 : destProgress)
      if (this.jump) {
        this.jump(this.dragProgress)
      }
    },
    dragend: async function (e) {
      if (!this.dragging) return
      if (this.jumpTo) {
        this.jumpTo(this.dragProgress)
      }
      await timeout(100)
      this.dragging = false
      this.dragProgress = 0
      this.startX = 0
    }
  }
}
</script>

<style scoped lang="less">
  @import "~assets/less/variable";

  @size: 2px;
  .wrapper {
    width: 100%;
    height: 100%;
    padding: 4px 0;
  }
  .progress-bar {
    position: relative;
    width: 100%;
    height: @size;
    background-color: rgba(255, 255, 255, .2);
    border-radius: @size;
    padding: 0 @size;
    box-sizing: border-box;
  }
  .progress {
    position: absolute;
    height: @size;
    left: 0;
    width: 0;
    background-color: @color-theme;
    border-radius: @size;
  }
  .progress-btn {
    position: absolute;
    left: 0;
    top: 50%;
    width: @size*2;
    height: @size*2;
    z-index: 1;
    background-color: @color-theme;
    border-radius: 50%;
    // box-sizing: border-box;
    // box-shadow: 0 0 0 @size*3 #fff;
    border: @size*3.5 solid #fff;
    transform: translate(-50%, -50%);
    cursor: pointer;
  }
</style>



