import jsonp from 'assets/js/jsonp'
import { commonParams, jsonpOptions } from './config'

export function getHotkey () {
  const url = `https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg`
  const postData = {
    ...commonParams,
    platform: 'h5',
    needNewCode: 1
  }
  return jsonp(url, postData, jsonpOptions)
}

export function search ({query, page = 1, pageSize = 20, zhida}) {
  const url = `https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp`
  const postData = {
    ...commonParams,
    w: query,
    p: page,
    catZhida: zhida ? 1 : 0,
    zhidaqu: 1,
    t: 0,
    flag: 1,
    ie: 'utf-8',
    sem: 1,
    aggr: 0,
    perpage: pageSize,
    n: pageSize,
    remoteplace: 'txt.mqq.all',
    uid: 0,
    needNewCode: 1,
    platform: 'h5'
  }
  return jsonp(url, postData, jsonpOptions)
}



