<template lang="html">
  <div class="player" v-show="playlist.length > 0">
    <playlist ref="playlist"></playlist>
    <transition name="normal"
      @enter="normalEnter"
      @after-enter="normalAfterEnter"
      @leave="normalLeave"
      @after-leave="normalAfterLeave"
      >
      <div class="normal-player" v-show="fullscreen">
        <div class="background" :style="{ backgroundImage: `url(${currentSong.image})` }">
          <div class="cover"></div>
        </div>
        <div class="top">
          <div class="hide" @click="fold">
            <icon class="icon-hide" name="angle-down" scale="2"></icon>
          </div>
          <h1 class="title" v-html="currentSong.name"></h1>
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <div class="middle" :class="{ 'show-lyric': showLyric }" @click="toggleLyric">
          <div class="middle-l">
            <div class="cd-wrapper" ref="cdWrapper">
              <rotate :rotating="playing" ref="rotate">
                  <div class="cd">
                    <img :src="currentSong.image" class="image">
                  </div>
              </rotate>
            </div>
          </div>
          <div class="middle-r">
            <scroller
              ref="lyricScroller"
              :data="lyric.lines || []"
              class="lyric-wrapper"
              >
              <div class="lyric">
                <div class="line" ref="lyricLine" v-for="(line, index) in lyric.lines" :class="{ active: index == currentLyricIndex }" v-html="line.value">
                </div>
              </div>
            </scroller>
          </div>
        </div>
        <div class="bottom">
          <div class="progress-wrapper">
            <span class="time time-l">{{ formatTime(currentTime) }}</span>
            <div class="progress-bar-wrapper">
              <progress-bar :progress="progress" :jumpTo="jumpTo" :jump="jump"></progress-bar>
            </div>
            <span class="time time-r">{{ formatTime(duration) }}</span>
          </div>
          <div class="operators">
            <div class="icon i-left" @click="changeMode">
              <icon class="icon-mode" :name="mode" scale="1.5"></icon>
            </div>
            <div class="icon i-left" @click="prevSong">
              <icon class="icon-prev" name="backward" scale="1.5"></icon>
            </div>
            <div class="icon i-center" @click="togglePlay">
              <icon class="icon-play" :name="playIcon" scale="1.5"></icon>
            </div>
            <div class="icon i-right" @click="nextSong">
              <icon class="icon-next" name="forward" scale="1.5"></icon>
            </div>
            <div class="icon i-right" @click="toggleFavorite">
              <icon class="icon-favorite" :name="favoriteIcon" scale="1.5"></icon>
            </div>
            <div class="icon i-right" @click.stop="showPlaylist">
              <icon class="icon-music" name="music" scale="1.5"></icon>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini">
      <div class="mini-player" v-show="!fullscreen" @click="unfold">
        <div class="progress-cover" :style="miniProgress"></div>
        <div class="icon">
          <rotate :rotating="playing" ref="miniRotate">
            <img :src="currentSong.image" width="40" height="40" ref="miniCD">
          </rotate>
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <div class="control" @click.stop="togglePlay">
          <icon class="icon-play" :name="playIcon" scale="1.2"></icon>
        </div>
        <div class="control" @click.stop="showPlaylist">
          <icon class="icon-playlist" name="music" scale="1.5"></icon>
        </div>
      </div>
    </transition>
    <audio
      @ended="onEnd"
      @error="onError"
      @play="onPlay"
      @timeupdate="update"
      @loadedmetadata="loadmeta"
      :src="currentSong.url"
      ref="audio"
      ></audio>
  </div>
</template>

<script>
  import { css, timeout, throttle, formatLyric } from 'assets/js/util'
  import { catchErrors } from 'api/handler'
  import animations from 'create-keyframe-animation'
  import * as mtypes from '@/store/mutation-types'
  import * as atypes from '@/store/action-types'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { SINGLE } from 'assets/js/config'
  import Song from 'assets/js/song'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/angle-down'
  import 'vue-awesome/icons/repeat'
  import 'vue-awesome/icons/backward'
  import 'vue-awesome/icons/play'
  import 'vue-awesome/icons/pause'
  import 'vue-awesome/icons/forward'
  import 'vue-awesome/icons/heart-o'
  import 'vue-awesome/icons/heart'
  import 'vue-awesome/icons/music'
  import 'vue-awesome/icons/recycle'
  import 'vue-awesome/icons/random'
  import 'vue-rotate/dist/assets/css/rotate.css'
  import Rotate from 'vue-rotate/dist/vue-rotate'
  import ProgressBar from 'components/base/progress-bar/progress-bar'
  import Scroller from 'components/base/scroller/scroller'
  import Playlist from 'components/playlist/playlist'

  export default {
    name: 'player',
    components: { Icon, Rotate, ProgressBar, Scroller, Playlist },
    created () {
      this.CDAnimationInfo = { x: 0, y: 0, scale: 1 }
    },
    data () {
      return {
        error: false,
        ready: false,
        currentTime: 0,
        duration: 0,
        lyric: {},
        currentLyricIndex: 0,
        showLyric: false,
        jumping: false
      }
    },
    computed: {
      ...mapGetters({
        playing: 'playing',
        fullscreen: 'fullscreen',
        playlist: 'playlist',
        mode: 'mode',
        currentSong: 'currentSong',
        currentIndex: 'index',
        favorite: 'favorite'
      }),
      favoriteIcon () {
        return this.favorite.findIndex(v => v.id === this.currentSong.id) >= 0 ? 'heart' : 'heart-o'
      },
      playIcon () {
        return this.playing ? 'pause' : 'play'
      },
      progress () {
        return this.duration === 0 ? 0 : this.currentTime / this.duration
      },
      percent () {
        return this.progress * 100 + '%'
      },
      miniProgress () {
        return `width: ${this.percent}`
      }
    },
    watch: {
      currentSong (newSong, oldSong) {
        if (!newSong.id || newSong.id === oldSong.id) return
        this.getLyric()
        this.restart()
      },
      playing () {
        const { audio } = this.$refs
        this.$nextTick(() => {
          this.playing ? audio.play() : audio.pause()
        })
      }
    },
    methods: {
      ...mapMutations({
        setFullscreen: mtypes.SET_FULLSCREEN,
        setPlaying: mtypes.SET_PLAYING,
        setCurrentIndex: mtypes.SET_CURRENT_INDEX,
        changeMode: mtypes.CHANGE_MODE,
        prev: mtypes.PREV,
        next: mtypes.NEXT
      }),
      ...mapActions({
        changeSong: atypes.CHANGE_SONG,
        cachePlay: atypes.CACHE_PLAY,
        cacheFavorite: atypes.CACHE_FAVORITE,
        decacheFavorite: atypes.DECACHE_FAVORITE
      }),
      loadmeta (e) {
        this.duration = Math.floor(e.target.duration)
      },
      getLyric: catchErrors(async function () {
        this.lyric = {}
        this.currentLyricIndex = 0
        const lyric = await Song.getLyric(this.currentSong)
        if (lyric) {
          this.lyric = formatLyric(lyric)
        }
      }),
      synchronizeLyric (time) {
        if (!this.lyric || !this.lyric.lines || this.lyric.lines.length < 1) return
        const offset = 600
        const index = this.lyric.lines.findIndex(line => line.time > (time * 1000 + offset)) - 1
        this.currentLyricIndex = index < -1 ? this.lyric.lines.length - 1 : (index < 0 ? 0 : index)
        const preLine = 7
        const jumpTo = this.currentLyricIndex - preLine
        this.$refs.lyricScroller.scrollToElement(this.$refs.lyricLine[jumpTo < 0 ? 0 : jumpTo], 800)
      },
      restart: async function () {
        this.error = false
        this.ready = false
        this.currentTime = 0
        this.pause()
        await timeout(50)
        this.$refs.rotate.reset()
        this.$refs.miniRotate.reset()
        this.play()
      },
      play () {
        if (!this.error) {
          this.setPlaying(true)
          this.cachePlay(this.currentSong)
        }
      },
      pause () {
        this.setPlaying(false)
      },
      onPlay () {
        this.ready = true
      },
      onEnd: async function () {
        // 结束时，如果是单曲循环或者列表中只有这一首歌就重新播放，否则就切换歌曲
        if (this.mode === SINGLE || this.playlist.length <= 1) {
          this.restart()
        } else {
          this.changeSong()
        }
      },
      onError (error) {
        this.error = error
        this.pause()
        this.$dialog.alert({
          okText: '知道了',
          title: '出错啦',
          desc: '可能是版权问题，无法播放此歌曲'
        })
      },
      togglePlay () {
        this.playing ? this.pause() : this.play()
      },
      update (e) {
        this.synchronizeLyric(e.target.currentTime)
        if (!this.jumping) {
          this.currentTime = e.target.currentTime
        }
      },
      formatTime (time) {
        const minute = Math.floor(time / 60)
        const second = Math.floor(time % 60)
        return `${minute}:${second < 10 ? '0' + second : second}`
      },
      jump (progress) {
        this.jumping = true
        this.currentTime = Math.floor(this.duration * progress)
      },
      jumpTo (progress) {
        this.jumping = false
        this.$refs.audio.currentTime = Math.floor(this.duration * progress)
      },
      fold () {
        this.setFullscreen(false)
      },
      unfold () {
        this.setFullscreen(true)
      },
      toggleLyric () {
        this.showLyric = !this.showLyric
      },
      prevSong: throttle(function () {
        if (this.ready) this.prev()
      }),
      nextSong: throttle(function () {
        if (this.ready) this.next()
      }),
      showPlaylist () {
        this.$refs.playlist.show()
      },
      toggleFavorite () {
        this.favorite.findIndex(v => v.id === this.currentSong.id) < 0 ? this.cacheFavorite(this.currentSong) : this.decacheFavorite(this.currentSong)
      },
      /**
       * 获取CD动画的信息
       * @return {Object} [包含横向纵向位移x, y和大小变化比例scale]
       */
      _getCDAnimationInfo () {
        const { cdWrapper, miniCD } = this.$refs
        const { width: w1, height: h1, left: l1, top: t1 } = cdWrapper.getBoundingClientRect()
        const { width: w2, height: h2, left: l2, top: t2 } = miniCD.getBoundingClientRect()
        return {
          x: l2 + h2 / 2 - l1 - h1 / 2,
          y: t2 + w2 / 2 - t1 - w1 / 2,
          scale: w2 / w1
        }
      },
      normalEnter (el, done) {
        const { x, y, scale } = this.CDAnimationInfo = this._getCDAnimationInfo()
        const animation = {
          0: {
            transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60: {
            transform: `translate3d(0,0,0) scale(1.1)`
          },
          100: {
            transform: `translate3d(0,0,0) scale(1)`
          }
        }
        animations.registerAnimation({
          name: 'normalEnterAnimation',
          animation,
          presets: {
            duration: 400,
            delay: 100,
            easing: 'linear'
          }
        })
        animations.runAnimation(this.$refs.cdWrapper, 'normalEnterAnimation', done)
      },
      normalAfterEnter () {
        animations.unregisterAnimation('normalEnterAnimation')
        css(this.$refs.cdWrapper, 'animation', '')
      },
      normalLeave (el, done) {
        const { x, y, scale } = this.CDAnimationInfo
        const animation = {
          0: {
            transform: `translate3d(0,0,0) scale(1)`
          },
          100: {
            transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
          }
        }
        animations.registerAnimation({
          name: 'normalLeaveAnimation',
          animation,
          presets: {
            duration: 200,
            easing: 'linear'
          }
        })
        animations.runAnimation(this.$refs.cdWrapper, 'normalLeaveAnimation', done)
      },
      normalAfterLeave () {
        animations.unregisterAnimation('normalLeaveAnimation')
        css(this.$refs.cdWrapper, 'animation', '')
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .normal-player {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 200;
    background: @color-background;
    .background {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.6;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      // filter: blur(20px);
      .cover {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .4);
        // opacity: .6;
        z-index: 1;
        // background: rgba(7, 17, 27, 0.25);
        backdrop-filter: blur(20px);
      }
    }
    .top {
      position: relative;
      margin-bottom: 25px;
      .hide {
        position: absolute;
        top: 0;
        left: 6px;
        z-index: 50;
        .icon-hide {
          display: block;
          padding: 0 8px 8px 8px;
          font-size: @font-size-large-x;
          color: @color-theme;
        }
      }
      .title {
        width: 70%;
        margin: 0 auto;
        line-height: 40px;
        text-align: center;
        .no-wrap();
        font-size: @font-size-large;
        color: @color-text;
      }
      .subtitle {
        line-height: 20px;
        text-align: center;
        font-size: @font-size-medium;
        color: @color-text;
      }
    }

    .middle {
      position: fixed;
      width: 100%;
      top: 80px;
      bottom: 120px;
      white-space: nowrap;
      font-size: 0;
      .middle-l {
        display: inline-block;
        vertical-align: top;
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 80%;
        transition: all .4s;
        .cd-wrapper {
          position: absolute;
          left: 10%;
          top: 0;
          width: 80%;
          height: 100%;
          .cd {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            border: 10px solid rgba(255, 255, 255, 0.1);
            border-radius: 50%;
            overflow: hidden;
            .image {
              display: block;
              width: 100%;
              height: 100%;
            }
          }
        }
      }
      .middle-r {
        position: absolute;
        left: 10%;
        top: 0;
        bottom: 0;
        width: 80%;
        margin: 0 auto;
        z-index: 200;
        opacity: 0;
        transition: all .4s;
        transform: translateX(100%);
        overflow: hidden;
        mask-image: linear-gradient(to bottom,rgba(255,255,255,0) 0,rgba(255,255,255,.6) 15%,rgba(255,255,255,1) 25%,rgba(255,255,255,1) 75%,rgba(255,255,255,.6) 85%,rgba(255,255,255,0) 100%);
        .lyric-wrapper {
          text-align: center;
          width: 100%;
          height: 100%;
          .line {
            color: @color-text;
            text-overflow: ellipsis;
            white-space: normal;
            line-height: 1.5;
            font-size: 12px;
            padding: 4px 0;
            opacity: .5;
            &.active {
              opacity: 1;
              font-weight: bold;
            }
          }
        }
      }
      &.show-lyric {
        .middle-l {
          opacity: 0;
        }
        .middle-r {
          opacity: 1;
          transform: translateX(0);
        }
      }
    }

    .bottom {
      position: absolute;
      bottom: 30px;
      width: 100%;
      .dot-wrapper {
        text-align: center;
        font-size: 0;
        .dot {
          display: inline-block;
          vertical-align: middle;
          margin: 0 4px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: @color-text-l;
          &.active {
            width: 20px;
            border-radius: 5px;
            background: @color-text-ll;
          }
        }
      }
      .progress-wrapper {
        display: flex;
        align-items: center;
        width: 80%;
        margin: 0 auto;
        padding: 10px 0;
        .time {
          color: @color-text;
          font-size: @font-size-small;
          flex: 0 0 30px;
          line-height: 30px;
          &.time-l {
            text-align: left;
          }
          &.time-r {
            text-align: right;
          }
        }
        .progress-bar-wrapper {
          flex: 1;
          padding: 8px;
          box-sizing: border-box;
        }
      }
      .operators {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 80%;
        margin: 0 auto;
        .icon {
          // flex: 1;
          color: @color-theme;
          width: 20px;
          line-height: 0;
          &.disabled {
            color: @color-theme-d;
          }
        }
        .icon-favorite {
          color: @color-theme;
        }
      }
    }
  }

  .mini-player {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 180;
    width: 100%;
    height: 60px;
    background: @color-highlight-background;
    .progress-cover {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      height: 100%;
      z-index: -1;
      background-color: #fff;
      opacity: .15;
    }
    .icon {
      flex: 0 0 40px;
      width: 40px;
      padding: 0 10px 0 20px;
      img {
        border-radius: 50%;
      }
    }
    .text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1;
      line-height: 20px;
      overflow: hidden;
      .name {
        margin-bottom: 2px;
        .no-wrap();
        font-size: @font-size-medium;
        color: @color-text;
      }
      .desc {
        .no-wrap();
        font-size: @font-size-small;
        color: @color-text-d;
      }
    }
    .control {
      padding: 0;
      .icon-playlist {
        margin-right: 10px;
      }
      .icon-play, .icon-playlist {
        font-size: 30px;
        color: @color-theme;
        padding: 10px 10px;
      }
      .icon-mini {
        font-size: 32px;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  .normal-enter-active, .normal-leave-active {
    transition: all .4s;
    .top, .bottom {
      transition: all .5s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      // transition: all .4s;
    }
  }
  .normal-enter, .normal-leave-to {
    opacity: 0;
    .top {
      transform: translate3d(0, -100px, 0);
    }
    .bottom {
      transform: translate3d(0, 100px, 0);
    }
  }
  .mini-enter-active, .mini-leave-active {
    transition: all .4s;
  }
  .mini-enter, .mini-leave-to {
    opacity: 0;
  }

</style>



