import * as atypes from '@/store/action-types'
import { mapActions, mapGetters } from 'vuex'
import { timeout } from 'assets/js/util'
import SearchBox from 'components/base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import Scroller from 'components/base/scroller/scroller'
import SearchList from 'components/base/search-list/search-list'
import Icon from 'vue-awesome/components/Icon'

export default {
  components: { SearchBox, Suggest, Scroller, Icon, SearchList },
  data () {
    return {
      query: '',
      refreshDelay: 300
    }
  },
  computed: {
    ...mapGetters([
      'searchHistory'
    ])
  },
  watch: {
    query: async function () {
      await timeout(20)
      if (this.query) {
        this.$refs.suggest.refresh()
      } else {
        this.$refs.scroller.refresh()
      }
    }
  },
  methods: {
    ...mapActions({
      cacheSearch: atypes.CACHE_SEARCH,
      decacheSearch: atypes.DECACHE_SEARCH,
      decacheSearchAll: atypes.DECACHE_SEARCH_ALL
    }),
    setQuery (query) {
      this.$refs.searchBox.setQuery(query)
    },
    queryChange (query) {
      this.query = query
    },
    blurInput () {
      this.$refs.searchBox.blur()
    },
    saveSearch () {
      this.cacheSearch(this.query)
    },
    clearSearchHistory () {
      this.$dialog.confirm({
        title: '是否删除所有搜索历史?',
        confirm: () => {
          this.decacheSearchAll()
        }
      })
    }
  }
}



