import axios from 'axios'
import { commonParams } from './config'

export function getLyric (songmid) {
  const url = `/api/lyric`
  const postData = {
    ...commonParams,
    songmid,
    pcachetime: +new Date(),
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    g_tk: 67232076,
    format: 'json'
  }
  return axios.get(url, { params: postData }).then(res => res.data)
}



