<template lang="html">
  <div class="container">
    <backdrop ref="backdrop"></backdrop>
    <transition name="popup">
      <div class="alert" v-show="state == 1">
        <div class="content">
          <div class="title" v-show="title">{{ title }}</div>
          <div class="desc" v-show="desc">{{ desc }}</div>
        </div>
        <div class="control">
          <div class="btn-ok" @click="onOk">{{ okText }}</div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Backdrop from './backdrop'
  import mixin from './mixin'

  export default {
    name: 'alert',
    mixins: [mixin],
    components: { Backdrop },
    data () {
      return {
        state: 0,
        title: '',
        desc: '',
        okText: '确定'
      }
    },
    methods: {
      onOk () {
        this.hide()
        this.ok && this.ok()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "./dialog.less";

  .alert {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: @translate;
    width: @width;
    text-align: center;
    z-index: 10001;
    background-color: rgba(255, 255, 255, 1);
    color: @color-highlight-background;
    font-size: @font-size-medium;
  }
  .content {
    padding: 10px;
    border-bottom: 1px solid #eee;
  }
  .title {
    margin: 5px 0 10px;
    box-sizing: border-box;
    font-weight: bold;
  }
  .desc {
    margin: 5px 0 10px;
    font-size: .8em;
    box-sizing: border-box;
  }
  .control {
    padding: 10px;
    box-sizing: border-box;
    .btn-ok {
      border-color: transparent;
      background-color: #4a90e2;
      color: #fff;
      padding: 10px 0;
    }
  }
</style>



