<template lang="html">
  <div class="container" @click="hide">
    <backdrop ref="backdrop"></backdrop>
    <transition name="slideUp">
      <div class="actionsheet" v-show="state == 1" @click.stop="hide">
        <div class="title" @click.stop>{{ title }}</div>
        <scroller ref="scroller" :refreshDelay="refreshDelay" class="actions" :data="actions" :maxHeight="163">
          <div class="action" v-for="action in actions" @click="action.operation">
            {{ action.desc }}
          </div>
        </scroller>
        <div class="cancel" @click="onCancel">{{ cancelText }}</div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Backdrop from './backdrop'
  import Scroller from 'components/base/scroller/scroller'
  import mixin from './mixin'

  export default {
    name: 'popup',
    mixins: [mixin],
    components: { Backdrop, Scroller },
    data () {
      return {
        title: '',
        actions: [],
        refreshDelay: 300,
        cancelText: '取消'
      }
    },
    methods: {
      onCancel () {
        this.hide()
        this.cancel && this.cancel()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "./actionsheet.less";

  .actionsheet {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    z-index: 10001;
    background-color: #eee;
    color: #000;
    font-size: 15px;
    box-sizing: border-box;

  }

  .title {
    font-size: 14px;
    height: 30px;
    line-height: 30px;
  }

  .actions {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    overflow: hidden;
    position: relative;
  }

  .action {
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    &:last-type-of {
      border-bottom: none;
    }
  }
  .cancel {
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    margin-top: 6px;
  }

</style>



