import Vue from 'vue'
import { createElement } from './util'
import Actionsheet from './actionsheet.vue'

let vm

export default {
  show (options) {
    if (!vm) {
      const el = createElement()
      vm = new Vue(Actionsheet).$mount(el)
    }
    vm.show(options)
  },
  hide () {
    vm && vm.hide()
  }
}



