import jsonp from 'assets/js/jsonp'
import { commonParams, jsonpOptions } from './config'

export function getSingerList () {
  const url = `https://c.y.qq.com/v8/fcg-bin/v8.fcg`
  const postData = {
    ...commonParams,
    platform: 'yqq',
    needNewCode: 0,
    channel: 'singer',
    page: 'list',
    key: 'all_all_all',
    pagesize: 100,
    pagenum: 1,
    hostUin: 0,
    g_tk: 1664029744
  }
  return jsonp(url, postData, jsonpOptions)
}

export function getSingerDetail (singerId) {
  const url = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg`
  const postData = {
    ...commonParams,
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq',
    order: 'listen',
    begin: 0,
    num: 100,
    songstatus: 1,
    g_tk: 1664029744,
    singermid: singerId
  }
  return jsonp(url, postData, jsonpOptions)
}



