<template lang="html">
  <transition name="fadein">
    <div class="playlist-wrapper" @click="hide" v-show="showFlag">
      <div class="playlist" @click.stop>
        <div class="header">
          <div class="icon" @click="changeMode">
            <icon class="icon-mode" :name="mode" scale="1.75"></icon>
          </div>
          <div class="text">{{ modeText }}</div>
          <div class="icon" @click="clear">
            <icon class="icon-clear" name="trash-o" scale="1.25"></icon>
          </div>
        </div>
        <scroller :maxHeight="listHeight" :data="playlist" ref="scroller" class="content" :refreshDelay="refreshDelay">
          <transition-group name="list" tag="div" ref="list" class="list">
            <div :key="item.id" ref="listItem" class="item" v-for="(item, i) in playlist" :style="{ height: itemHeight+'px' }" @click="seleteItem(i)">
              <div class="current icon">
                <icon class="icon-play" :name="playIcon" scale="1.2" v-show="i == index"></icon>
              </div>
              <div class="name" v-html="`${item.name} - ${item.singer}`"></div>
              <div class="icon" @click.stop="toggleFavorite(item)">
                <icon class="icon-like" :name="favoriteIcon(item)" scale="1"></icon>
              </div>
              <div class="icon" @click.stop="removeSong(i)">
                <icon class="icon-remove" name="remove" scale="1.25"></icon>
              </div>
            </div>
          </transition-group>
        </scroller>
        <div class="add-to-list">
          <div class="btn" @click="showAddSong">
            <icon class="icon-plus" name="plus"></icon>
            添加歌曲
          </div>
        </div>
        <div class="close" @click="hide">
          关闭
        </div>
        <add-song ref="addSong"></add-song>
      </div>
    </div>
  </transition>
</template>

<script>
  import * as mtypes from '@/store/mutation-types'
  import * as atypes from '@/store/action-types'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { LOOP, RANDOM, SINGLE } from 'assets/js/config'
  import { timeout } from 'assets/js/util'
  import Scroller from 'components/base/scroller/scroller'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/play-circle-o'
  import 'vue-awesome/icons/pause-circle-o'
  import 'vue-awesome/icons/repeat'
  import 'vue-awesome/icons/recycle'
  import 'vue-awesome/icons/random'
  import 'vue-awesome/icons/heart-o'
  import 'vue-awesome/icons/remove'
  import 'vue-awesome/icons/trash-o'
  import 'vue-awesome/icons/plus'
  import AddSong from 'components/add-song/add-song'

  export default {
    name: 'playlist',
    components: { Icon, Scroller, AddSong },
    data () {
      return {
        showFlag: false,
        refreshDelay: 300,
        itemHeight: 40
      }
    },
    watch: {
      currentSong (newSong, oldSong) {
        if (!newSong.id) {
          return this.hide()
        }
        if (newSong.id === oldSong.id) return
        this.scrollToCurrent()
      },
      async index () {
        await timeout(this.refreshDelay + 50)
        // 当添加歌曲时，即当前index是最后一个，则滚动到当前
        if (this.index === this.playlist.length - 1) {
          this.scrollToCurrent()
        }
      }
    },
    computed: {
      ...mapGetters([
        'playing', 'mode', 'index', 'playlist', 'currentSong', 'favorite'
      ]),
      listHeight () {
        return this.itemHeight * 6
      },
      modeText () {
        const map = {
          [LOOP]: '列表循环',
          [SINGLE]: '单曲循环',
          [RANDOM]: '随机播放'
        }
        return map[this.mode]
      },
      playIcon () {
        return this.playing ? 'play-circle-o' : 'pause-circle-o'
      }
    },
    methods: {
      ...mapMutations({
        setPlaying: mtypes.SET_PLAYING,
        setCurrentIndex: mtypes.SET_CURRENT_INDEX,
        changeMode: mtypes.CHANGE_MODE
      }),
      ...mapActions({
        removeSong: atypes.REMOVE_SONG,
        clearPlaylist: atypes.CLEAR_PLAYLIST,
        cacheFavorite: atypes.CACHE_FAVORITE,
        decacheFavorite: atypes.DECACHE_FAVORITE
      }),
      clear () {
        this.$dialog.confirm({
          title: '是否清空歌曲列表',
          confirm: () => {
            this.clearPlaylist()
          }
        })
      },
      async scrollToCurrent () {
        if (this.showFlag) {
          await timeout(50)
          this.$refs.scroller.scrollToElement(this.$refs.list.$el.children[this.index], 200)
        }
      },
      seleteItem (index) {
        if (this.index === index) {
          this.setPlaying(!this.playing)
        } else {
          this.setCurrentIndex(index)
        }
      },
      show () {
        this.showFlag = true
        this.$refs.scroller.refresh(() => {
          this.scrollToCurrent()
        })
      },
      hide () {
        this.showFlag = false
      },
      showAddSong () {
        this.$refs.addSong.show()
      },
      favoriteIcon (song) {
        return this.favorite.findIndex(v => v.id === song.id) >= 0 ? 'heart' : 'heart-o'
      },
      toggleFavorite (song) {
        this.favorite.findIndex(v => v.id === song.id) < 0 ? this.cacheFavorite(song) : this.decacheFavorite(song)
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .playlist-wrapper {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 300;
    background-color: @color-background-d;;
  }

  .playlist {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: @color-highlight-background;
    font-size: @font-size-medium;
  }

  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px 10px;
    .icon {
      color: @color-theme;
    }
    .icon-mode {
      width: 24px;
    }
    .text {
      font-weight: bold;
      color: @color-text;
      margin: 0 15px;
      flex: 1;
    }
    .clear {
      .extend-click();
      color: @color-text;
    }
  }

  .content {
    overflow: hidden;
    position: relative;
    .list {
      .item {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding: 0 20px;
        overflow: hidden;
        font-size: @font-size-small;
        .current {
          min-width: 20px;
        }
        .name {
          flex: 1;
          color: @color-text-l;
          margin: 0 10px;
          text-overflow: ellipsis;
        }
        .icon {
          .extend-click();
          line-height: 0;
        }
        .icon-like {
          margin-right: 12px;
        }
      }
    }
  }

  .add-to-list {
    text-align: center;
    margin: 20px auto;
    color: @color-text-ll;
    .btn {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      padding: 6px 15px;
      border-radius: 20px;
      border: 1px solid @color-text-ll;
      .icon-plus {
        margin-right: 5px;
      }
    }
  }

  .close {
    background-color: @color-background;
    color: @color-text-l;
    padding: 15px;
    text-align: center;
  }

  .fadein-enter-active, .fadein-leave-active {
    transition: all .25s;
    .playlist {
      transition: all .25s;
    }
  }
  .fadein-enter, .fadein-leave-to {
    opacity: 0;
    .playlist {
      transform: translate3d(0, 100%, 0);
    }
  }

  .list-enter-active, .list-leave-active {
    transition: all .2s;
  }
  .list-enter, .list-leave-to {
    opacity: 0;
    height: 0 !important;
  }
</style>



