import { extend } from './util'

export default {

  data () {
    return {
      state: 0
    }
  },

  destroyed () {
    const parent = this.$el.parentNode
    parent.removeChild(this.$el)
  },

  methods: {
    show (options) {
      extend(this, options)
      this.state = 1
      const { backdrop } = this.$refs
      backdrop && backdrop.show()
    },
    hide () {
      this.state = 0
      const { backdrop, scroller } = this.$refs
      backdrop && backdrop.hide()
      scroller && scroller.scrollTo(0, 0)
    }
  }
}



