import Singer from 'assets/js/singer'
import { getDiscList } from 'api/recommend'
import { getSingerList } from 'api/singer'
import { getTopList } from 'api/rank'
import { ERR_OK } from 'api/config'
import { catchErrors } from 'api/handler'
import { timeout } from 'assets/js/util'
import { myDiscPrefix } from 'assets/js/config'
import * as mtypes from './mutation-types'
import * as atypes from './action-types'
import { cacheSearch, decacheSearch, cacheFavorite, decacheFavorite, cacheCollect, decacheCollect, cacheMyDiscList } from 'assets/js/cache'

const HOT_NAME = '热门'
const HOT_LEN = 10

/**
 * 格式化歌手数据
 * @param  {[type]} singerList 歌手列表
 */
function formatData (singerList) {
  let map = {}
  let hot = {
    title: HOT_NAME,
    items: []
  }
  singerList.forEach((v, i) => {
    const { Findex, Fsinger_mid, Fsinger_name } = v
    const singer = new Singer({ id: Fsinger_mid, name: Fsinger_name })
    if (i < HOT_LEN) {
      hot.items.push(singer)
    }
    if (!map[Findex]) {
      map[Findex] = {
        title: Findex,
        items: []
      }
    }
    map[Findex].items.push(singer)
  })
  // 排序
  const keys = Object.keys(map).sort((a, b) => a > b ? 1 : -1)
  return [
    hot, ...keys.map(k => map[k])
  ]
}

const actions = {
  /**
   * 获取推荐歌单列表
   */
  [atypes.GET_RECOMMEND_DISC_LIST]: catchErrors(async function ({ commit }) {
    await timeout(500)
    const discListRes = await getDiscList()
    if (discListRes && discListRes.code === ERR_OK) {
      const discList = discListRes.data.list || []
      commit(mtypes.SET_RECOMMEND_DISC_LIST, discList.map(disc => ({
        id: disc.dissid,
        creator: disc.creator.name,
        name: disc.dissname,
        imgurl: disc.imgurl
      })))
    }
  }),
  /**
   * 获取歌手列表
   */
  [atypes.GET_SINGER_LIST]: catchErrors(async function ({ commit }) {
    await timeout(500)
    const singerListRes = await getSingerList()
    if (singerListRes && singerListRes.code === ERR_OK) {
      const singerGroupList = formatData(singerListRes.data.list || [])
      commit(mtypes.SET_SINGER_LIST, singerGroupList)
    }
  }),
  /**
   * 获取排行列表
   */
  [atypes.GET_TOP_LIST]: catchErrors(async function ({ commit }) {
    await timeout(500)
    const topListRes = await getTopList()
    if (topListRes && topListRes.code === ERR_OK) {
      const topList = topListRes.data.topList || []
      commit(mtypes.SET_TOP_LIST, topList)
    }
  }),
  /**
   * 缓存搜索历史
   */
  [atypes.CACHE_SEARCH] ({ commit }, query) {
    const search = cacheSearch(query)
    commit(mtypes.SET_SEARCH_HISTORY, search)
  },
  /**
   * 删除缓存搜索历史
   */
  [atypes.DECACHE_SEARCH] ({ commit }, query) {
    const search = decacheSearch(query)
    commit(mtypes.SET_SEARCH_HISTORY, search)
  },
  /**
   * 删除所有缓存搜索历史
   */
  [atypes.DECACHE_SEARCH_ALL] ({ commit }) {
    const search = decacheSearch()
    commit(mtypes.SET_SEARCH_HISTORY, search)
  },
  /**
   * 缓存喜欢的歌
   */
  [atypes.CACHE_FAVORITE] ({ commit }, song) {
    const favorite = cacheFavorite(song)
    commit(mtypes.SET_FAVORITE, favorite)
  },
  /**
   * 删除缓存喜欢的歌
   */
  [atypes.DECACHE_FAVORITE] ({ commit }, song) {
    const favorite = decacheFavorite(song)
    commit(mtypes.SET_FAVORITE, favorite)
  },
  /**
   * 删除所有缓存喜欢的歌
   */
  [atypes.DECACHE_FAVORITE_ALL] ({ commit }) {
    const favorite = decacheFavorite()
    commit(mtypes.SET_FAVORITE, favorite)
  },
  /**
   * 缓存收藏的歌单
   */
  [atypes.CACHE_COLLECT] ({ commit }, disc) {
    const collect = cacheCollect(disc)
    commit(mtypes.SET_COLLECT_DISC, collect)
  },
  /**
   * 删除缓存收藏的歌单
   */
  [atypes.DECACHE_COLLECT] ({ commit }, disc) {
    const collect = decacheCollect(disc)
    commit(mtypes.SET_COLLECT_DISC, collect)
  },
  /**
   * 删除所有缓存收藏的歌单
   */
  [atypes.DECACHE_COLLECT_ALL] ({ commit }) {
    const collect = decacheCollect()
    commit(mtypes.SET_COLLECT_DISC, collect)
  },
  /**
   * 创建新歌单
   */
  [atypes.CREATE_MY_DISC] ({ commit, state }, name) {
    const newDisc = {
      id: `${myDiscPrefix}${Date.now()}`,
      creator: name,
      name: name,
      songs: []
    }
    commit(mtypes.CREATE_MY_DISC, newDisc)
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 删除我的歌单
   */
  [atypes.DELETE_MY_DISC] ({ commit, state }, disc) {
    commit(mtypes.DELETE_MY_DISC, disc)
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 删除所有我的歌单
   */
  [atypes.DELETE_MY_DISC_ALL] ({ commit, state }) {
    commit(mtypes.DELETE_MY_DISC_ALL)
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 向我的歌单中添加歌曲
   */
  [atypes.ADD_SONG_TO_MY_DISC] ({ commit, state }, { disc, song }) {
    commit(mtypes.ADD_SONG_TO_MY_DISC, { dissid: disc.id, song })
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 从我的歌单中移除歌曲
   */
  [atypes.REMOVE_SONG_FROM_MY_DISC] ({ commit, state }, { disc, song }) {
    commit(mtypes.REMOVE_SONG_FROM_MY_DISC, { dissid: disc.id, songid: song.id })
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 从我的歌单中移除全部歌曲
   */
  [atypes.CLEAR_MY_DISC] ({ commit, state }, disc) {
    commit(mtypes.CLEAR_MY_DISC, disc.id)
    cacheMyDiscList(state.myDiscList)
  },
  /**
   * 修改我的歌单名称
   */
  [atypes.CHANGE_MY_DISC_NAME] ({ commit, state }, { disc, name }) {
    commit(mtypes.CHANGE_MY_DISC_NAME, { dissid: disc.id, name })
    cacheMyDiscList(state.myDiscList)
  }
}

export default actions



