<template lang="html">
  <transition name="fadein">
    <div class="backdrop" v-show="state == 1" @click="click"></div>
  </transition>
</template>

<script>
export default {
  name: 'backdrop',
  data () {
    return {
      state: 0
    }
  },
  methods: {
    show () {
      this.state = 1
    },
    hide () {
      this.state = 0
    },
    click () {
      this.$emit('click')
    }
  }
}
</script>

<style scoped lang="less">
  @import "./actionsheet.less";

  .backdrop {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
    background-color: rgba(0, 0, 0, .3);
  }
</style>



