<template lang="html">
  <div class="wrapper">
    <div class="pulldown"
      v-if="pulldownRefresh"
      :class="{'active': pulldownStatus == 1, 'active refreshing': pulldownStatus == 2, 'success': pulldownStatus == 3, 'recover': pulldownStatus == 4}"
      ref="pulldown"
      >
      <div class="content">
        <div class="pulldown-hint" v-if="pulldownStatus < 2">
          <div class="pulldown-icon">
            <icon name="long-arrow-down" scale="1.5"></icon>
          </div>
          <div class="pulldown-text">下拉刷新</div>
        </div>
        <div class="pulldown-refresh-icon" v-if="pulldownStatus == 2">
          <icon name="refresh" scale="1.2" spin></icon>
        </div>
        <div class="pulldown-success-icon" v-if="pulldownStatus >= 3">
          <icon name="check-circle-o" scale="1.5"></icon>
        </div>
      </div>
    </div>
    <div class="scroller" ref="scroller" @touchend="touchend" @mouseup="touchend" :style="scrollerStyle">
      <div
      class="content"
      :class="{ 'active': pulldownStatus == 2, 'success': pulldownStatus == 3, 'recover': pulldownStatus == 4 }"
      >
          <slot></slot>
          <div class="pullup" v-show="hasMore" v-if="pullupLoad">
            <div class="loading-icon">
              <loading :scale="1.2"></loading>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
  // 只有引入的是probe版本的iscroll才能使用onScroll事件
  import IScroll from 'iscroll/build/iscroll-probe'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/long-arrow-down'
  import 'vue-awesome/icons/refresh'
  import 'vue-awesome/icons/check-circle-o'
  import Loading from 'components/base/loading/loading3'

  import { timeout } from 'assets/js/util'
  import { catchErrors } from 'api/handler'

  export default {
    name: 'scroller',
    components: { Icon, Loading },
    props: {
      data: {
        type: Array,
        required: true
      },
      probeType: {
        type: Number,
        default: 1
      },
      refreshDelay: {
        type: Number,
        default: 50
      },
      onScroll: Function,
      pulldownRefresh: Function,
      pullupLoad: Function,
      hasMore: {
        type: Boolean,
        default: false
      },
      maxHeight: Number
    },
    mounted: catchErrors(async function () {
      await timeout(20)
      this.scroller = new IScroll(this.$refs.scroller, {
        eventPassthrough: 'horizontal',
        // disableMouse: true,
        // click: false,
        // disablePointer: true,
        // disableTouch: false,
        // mouseWheel: true,
        probeType: this.pulldownRefresh || this.onScroll ? 3 : this.probeType
      })
      if (this.pulldownRefresh) this._initPulldownRefresh()
      if (this.pullupLoad) this._initPullupLoad()
      if (this.onScroll) this._configOnScroll()
      await timeout(this.refreshDelay)
      this.refresh()
    }),
    data () {
      return {
        scroller: null,
        pulldownStatus: 0
      }
    },
    computed: {
      scrollerStyle () {
        return {
          maxHeight: this.maxHeight ? `${this.maxHeight}px` : 'none'
        }
      }
    },
    watch: {
      data () {
        this.refresh()
      },
      pulldownStatus () {
        if (this.pulldownStatus <= 1) {
          this.enable()
        } else {
          this.disable()
        }
      }
    },
    methods: {
      /**
       * 初始化下拉刷新
       */
      _initPulldownRefresh () {
        const self = this
        this.scroller.on('scroll', function () {
          const { y } = this
          if (y >= 60) {
            if (self.pulldownStatus < 1) {
              self.pulldownStatus = 1
            }
          } else {
            if (self.pulldownStatus === 1) {
              self.pulldownStatus = 0
            } else if (self.pulldownStatus === 0) {
              self.$refs.pulldown.style.transform = `translateY(${y}px)`
            }
          }
        })
      },
      /**
       * 初始化上拉加载
       */
      _initPullupLoad () {
        const self = this
        this.scroller.on('scrollEnd', function () {
          const { y, maxScrollY } = this
          if (y <= maxScrollY + 20) {
            self.pullupLoad()
          }
        })
      },
       /**
       * 配置onscroll事件
       */
      _configOnScroll () {
        const self = this
        this.scroller.on('scroll', function () {
          const { x, y } = this
          self.onScroll({ x, y })
        })
      },
      /**
       * 给外部组件用来动态调用scroller刷新
       */
      refresh: catchErrors(async function (cb) {
        await timeout(this.refreshDelay)
        this.scroller && this.scroller.refresh()
        cb && cb()
      }),
      /**
       * 松手时的相关处理，主要针对下拉刷新
       */
      touchend () {
        if (this.pulldownStatus === 1) {
          this.pulldownStatus = 2
          this.pulldownRefresh(async () => {
            await timeout(this.scroller.options.bounceTime)
            this.pulldownStatus = 3
            await timeout(1000)
            this.pulldownStatus = 4
            await timeout(200)
            this.pulldownStatus = 0
            // 在还原的时候顺便还原pulldown被scoll改变的translateY
            this.$refs.pulldown.style.transform = `translateY(0)`
          })
        }
      },
      /**
       * 调用iscroll的scrollToElement方法
       */
      scrollToElement () {
        this.scroller.scrollToElement.apply(this.scroller, arguments)
      },
      /**
       * 调用iscroll的scrollToElement方法
       */
      scrollTo () {
        this.scroller.scrollTo.apply(this.scroller, arguments)
      },
      enable () {
        this.scroller.enable()
      },
      disable () {
        this.scroller.disable()
      }
    }
  }
</script>

<style lang="less" scoped>
  @import "~assets/less/variable";

  @pulldownHeight: 60px;
  @recoverDuration: .2s;
  .scroller {
    height: 100%;
    width: 100%;
    .content {
      position: relative;
      &.active {
        transform: translate3d(0, @pulldownHeight, 0) !important;
        transition: transform .1s;
      }
      &.success {
        margin-top: @pulldownHeight;
      }
      &.recover {
        transition: margin-top @recoverDuration;
      }
    }
  }

  .pulldown {
    position: relative;
    width: 100%;
    text-align: center;
    color: @color-text-l;
    font-size: .8em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: @pulldownHeight;
    margin-top: -@pulldownHeight;
    .pulldown-icon {
      padding: 4px 0;
      transition: transform .25s;
      transform: translate3d(0,0,0) rotate(0);
      overflow: hidden;
    }
    .pulldown-text {
      padding: 4px 0;
    }
    .pulldown-refresh-icon, .pulldown-success-icon {
      padding: 5px 0;
      box-sizing: border-box;
    }
    &.active {
      .pulldown-icon {
        transform: translate3d(0,0,0) rotate(180deg);
      }
    }
    &.recover {
      transform: translateY(0) !important;
      transition: transform @recoverDuration;
    }
  }

  .pullup {
    text-align: center;
    .loading-icon {
      color: @color-theme;
      padding-bottom: 20px;
      box-sizing: border-box;
    }
  }

</style>



