import { getLyric } from 'api/song'
import { ERR_OK } from 'api/config'
import { Base64 } from 'js-base64'

export default class Song {
  constructor ({ id, mid, singer, name, album, albumid, duration, image, url }) {
    this.id = id
    this.mid = mid
    this.singer = singer
    this.name = name
    this.albumid = albumid
    this.album = album
    this.duration = duration
    this.image = image
    this.url = url
  }
  static getLyric (song) {
    if (song.lyric) return song.lyric
    return getLyric(song.mid).then(data => {
      if (data.code === ERR_OK) {
        song.lyric = Base64.decode(data.lyric)
        return song.lyric
      }
    })
  }
  static create ({ songid, songmid, singer, songname, albumname, albummid, interval }) {
    return new Song({
      id: songid,
      mid: songmid,
      singer: singer ? singer.map(s => s.name).join('/') : '',
      name: songname,
      album: albumname,
      albumid: albummid,
      duration: interval,
      image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${albummid}.jpg?max_age=2592000`,
      url: `http://ws.stream.qqmusic.qq.com/C100${songmid}.m4a?fromtag=66`
    })
  }
}



