import * as types from '@/store/mutation-types'
import { playMode } from 'assets/js/config'

const mutations = {
  [types.SET_PLAYING] (state, playing) {
    state.playing = playing
  },
  [types.SET_FULLSCREEN] (state, fullscreen) {
    state.fullscreen = fullscreen
  },
  [types.SET_PLAYLIST] (state, playlist = []) {
    state.playlist = playlist.slice()
  },
  [types.CHANGE_MODE] (state, mode) {
    const index = playMode.indexOf(state.mode)
    state.mode = playMode[(index + 1) % playMode.length]
  },
  [types.SET_MODE] (state, mode) {
    state.mode = mode
  },
  [types.SET_CURRENT_INDEX] (state, index) {
    state.index = index
  },
  [types.NEXT] (state) {
    let index = state.index + 1
    if (index >= state.playlist.length) {
      index = 0
    }
    state.index = index
  },
  [types.PREV] (state) {
    let index = state.index - 1
    if (index < 0) {
      index = state.playlist.length - 1
    }
    state.index = index
  },
  [types.RANDOM] (state) {
    let newIndex = state.index
    while (state.index === newIndex) {
      newIndex = Math.floor(Math.random() * state.playlist.length)
    }
    state.index = newIndex
  },
  [types.ADD_SONG] (state, song) {
    state.playlist.push(song)
  },
  [types.SET_PLAY_HISTORY] (state, play) {
    state.playHistory = play
  }
}

export default mutations



