const getters = {
  recommendDiscList: state => state.recommendDiscList,
  singerGroupList: state => state.singerGroupList,
  singerFlatList: state => {
    const singerFlatList = []
    state.singerGroupList.forEach(group => {
      group.items.forEach(singer => {
        singerFlatList.push(singer)
      })
    })
    return singerFlatList
  },
  topList: state => state.topList,
  searchHistory: state => state.searchHistory,
  favorite: state => state.favorite,
  collectDiscList: state => state.collectDiscList,
  myDiscList: state => state.myDiscList
}

export default getters



