import { cacheSearch, cacheFavorite, cacheCollect, cacheMyDiscList } from 'assets/js/cache'

const state = {
  recommendDiscList: [],
  topList: [],
  singerGroupList: [],
  searchHistory: cacheSearch() || [],
  favorite: cacheFavorite() || [],
  collectDiscList: cacheCollect() || [],
  myDiscList: cacheMyDiscList() || []
}

export default state



