import Vue from 'vue'
import { createElement } from './util'
import Toast from './toast.vue'

let vm

export default {
  show (toast) {
    if (!vm) {
      const el = createElement()
      vm = new Vue(Toast).$mount(el)
    }
    vm.show(toast)
  },
  success (message) {
    this.show({ type: 'success', message })
  },
  info (message) {
    this.show({ type: 'info', message })
  },
  error (message) {
    this.show({ type: 'error', message })
  },
  hide () {
    vm && vm.hide()
  }
}



