import Vue from 'vue'
import { createElement } from './util'
import Popup from './popup.vue'

let vm

export default {
  show (options) {
    if (!vm) {
      const el = createElement()
      vm = new Vue(Popup).$mount(el)
    }
    vm.show(options)
  },
  hide () {
    vm && vm.hide()
  }
}



