<template lang="html">
  <ul>
    <li v-for="(item, i) in list" :key="item" @click="change(i)" :class="{ active: i === index }">
      {{ item }}
    </li>
  </ul>
</template>

<script>
  export default {
    name: 'tab-btn',
    props: {
      list: {
        type: Array,
        default: []
      },
      index: {
        type: Number,
        default: 0
      }
    },
    methods: {
      change (index) {
        this.$emit('change', index)
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";

  ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 1px solid @color-highlight-background;
    color: @color-text-d;
    border-radius: 4px;
  }
  li {
    flex: 1;
    padding: 8px 0;
    border-right: 1px solid @color-highlight-background;
    &:last-child {
      border-right: none;
    }
    &.active {
      background-color: @color-highlight-background;
      color: @color-text;
    }
  }
</style>



